import { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import Web3 from 'web3';
import { connectWallet, signMessage, verifySignature } from '@/lib/web3/auth';

interface Web3AuthContextType {
  web3: Web3 | null;
  account: string | null;
  chainId: bigint | null;
  connect: () => Promise<void>;
  disconnect: () => void;
  isAuthenticated: boolean;
  authToken: string | null;
}

const Web3AuthContext = createContext<Web3AuthContextType | undefined>(undefined);

export const Web3AuthProvider = ({ children }: { children: ReactNode }) => {
  const [web3, setWeb3] = useState<Web3 | null>(null);
  const [account, setAccount] = useState<string | null>(null);
  const [chainId, setChainId] = useState<bigint | null>(null);
  const [authToken, setAuthToken] = useState<string | null>(null);

  const connect = async () => {
    try {
      const { web3, account, chainId } = await connectWallet();
      setWeb3(web3);
      setAccount(account);
      setChainId(chainId);

      // Generate a signature for backend verification
      const message = `Welcome to our dApp! Please sign this message to verify your wallet. Nonce: ${Date.now()}`;
      const signature = await signMessage(web3, account, message);

      // In a real app, you would send this to your backend for verification
      const isValid = await verifySignature(web3, message, signature, account);
      if (isValid) {
        setAuthToken(signature); // In production, use a proper JWT from your backend
      }

      // Listen for account changes
      if (window.ethereum) {
        window.ethereum.on('accountsChanged', (accounts: string[]) => {
          if (accounts.length > 0) {
            setAccount(accounts[0]);
          } else {
            disconnect();
          }
        });

        window.ethereum.on('chainChanged', () => {
          window.location.reload();
        });
      }
    } catch (error) {
      console.error('Error connecting wallet:', error);
      throw error;
    }
  };

  const disconnect = () => {
    setWeb3(null);
    setAccount(null);
    setChainId(null);
    setAuthToken(null);
  };

  useEffect(() => {
    // Check if wallet is already connected when component mounts
    const checkConnectedWallet = async () => {
      if (window.ethereum && window.ethereum.selectedAddress) {
        try {
          await connect();
        } catch (error) {
          console.error('Error reconnecting wallet:', error);
        }
      }
    };

    checkConnectedWallet();

    return () => {
      if (window.ethereum) {
        window.ethereum.removeAllListeners();
      }
    };
  }, []);

  return (
    <Web3AuthContext.Provider
      value={{
        web3,
        account,
        chainId,
        connect,
        disconnect,
        isAuthenticated: !!authToken,
        authToken,
      }}
    >
      {children}
    </Web3AuthContext.Provider>
  );
};

export const useWeb3Auth = () => {
  const context = useContext(Web3AuthContext);
  if (context === undefined) {
    throw new Error('useWeb3Auth must be used within a Web3AuthProvider');
  }
  return context;
};